import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { GetProductsProvider } from '../../providers/get-products/get-products';
import {ProductPage} from "../product/product";



export interface webProducten{
    id: string;
    name: string;
    price: string;
    summary: string;
    description: string;
    image: string;
    color: string;
}



@Component({
    selector: 'page-home',
    templateUrl: 'home.html'
})
export class HomePage {


    productenList: Array<webProducten> = [];
    backgroundColor: Array<any> = ["#827B00", '#898989', '#FFF7E2', '#5F292E'];
    isActive: boolean = false;


    constructor(public navCtrl: NavController, private getProducts: GetProductsProvider) {

        this.getAllProducts();

    }

    // aan de hand van de object id alignment image bepalen
    getImageBackgroundClass(id){
        if(this.checkOdd(id)){
            return 'backgroundLeft';
        }else {return 'backgroundRight';}
    }

    // aan de hand van product ID alignment content bepalen
    getAlignment(id){
        if(this.checkOdd(id)){
            return 'left';
        }else {return 'right';}
    }

    // aan de hand van product ID image class bepalen
    getImageClass(id){
        if(this.checkOdd(id)){
            return 'productImageLeft';
        }else {return 'productImageRight';}

    }

    // aan de hand van product ID block class bepalen (achtergrond image)
    getBlockClass(id){
        if(this.checkOdd(id)){
            return 'homeItemsLeft';
        }else {return 'homeItemsRight';}
    }

    //checken of de product ID even/oneven is
    checkOdd(id){
        if(id&1){
            return true;
        }
    }

    getAllProducts(){
        // Api producten ophalen met get-products provider
        this.getProducts.getProducts().subscribe(data => {
            this.productenList = data;
            // aan elk object attribute color en de waarde van color meegeven
            for (var i = 0; i < this.productenList.length; i ++){
                Object.assign(this.productenList[i], {color: this.backgroundColor[i]});
            }
        });

    }

    //click action, object wordt als parameter meegegeven
    openProductPage(productItem): void{
        this.navCtrl.push(ProductPage, {data: productItem });
    }


}


