import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Http, Response} from "@angular/http";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Observable';
import { HttpClientModule } from '@angular/common/http';
import { HttpModule } from '@angular/http';

/*
  Generated class for the GetProductsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class GetProductsProvider {

    private url: string = 'http://vps101.tjuna.com:1982/api/products';

    constructor(private http: Http) {
    }

    getProducts(){
        return this.http.get(this.url)
            .map(this.extractData)
            .do(this.logResponse)
            .catch(this.catchError);
    }

    // getting reponse
    private logResponse(res: Response){
        console.log(res);
    }

    //extract data from response
    private extractData(res: Response){
/*        var arrOfObj = res.json();
        arrOfObj.map(function (obj) {
            obj.color = '';
        });
        return arrOfObj;*/

        return res.json();
    }

    //error handeling
    private catchError(error: Response | any){
        console.log(error);
        return Observable.throw(error.json().error || "Server error. Try later again!");
    }

}



